body, .root, html{
  padding: 2em;
  /* display: flex; */
  background-color: black;
  color: red;
  height: 100%;
}

button{
  background-color: red;
  border-radius: 1em;
  padding: 1em;
  font-weight: bolder;
  color: white;
  border: none;
}
button:hover{
  background-color: white;
  border-radius: 1em;
  padding: 1em;
  font-weight: bolder;
  color: red;
  border: none;
}
.input-field{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
input{
  padding: 1em;
  border-radius: 1em;
  border: none;
  width: 100%;
  margin: 0.5em;
}